* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
body {
  margin:0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #f5f5f5;
}

button {
  background:#111; 
  border: none;
  color: #fff;
  padding: 10px 30px;
  text-align: center;
  text-decoration: none;
  /* display:block; */
  font-size: 16px;
  cursor: pointer;
  font-weight: bold;
  border-radius: 5px;
}
.navbar {
  position: sticky;
  top:0px;
  left:0px;
  width:100%;
  height:50px;
  display:flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-bottom: 1px solid #d5d5d5;
}
.navbar .logo {
  color: #111;
}
.quiz-container {
  position:relative;
  margin:50px auto 20px;
  width:95%;
  max-width: 650px;
  min-height: 350px;
  background: #fff;
  border:1px solid #eee;
  box-shadow: 0px 2px 5px 2px rgba(0, 0, 0, 0.3);
}
.join-screen{
  width:100%;
  height: 350px;
  display: inline-block;
  flex-direction: column;
  justify-content: center;
  gap:3em;
  text-align: center;
  padding:40px;
  margin:auto;
}
.join-screen h2 {
  font-size:25px;
  color:#111;
}
.join-screen h3{
  /* max-width: 350px; */
  color: #555;
}
.quiz-screen .question .progress-bar {
  width:0%;
  height:5px;
}
.quiz-screen .question .progress-bar .active{
  animation:progress 10s linear forwords;
}
.quiz-screen .question .question-count {
  margin:20px 0px;
  text-align: center;
  font-size: 15px;
  color:#555;
}
.quiz-screen .question .main{
  padding:20px 40px;
}
.quiz-screen .question .main .title span{
  font-size:15px;
  color:#555;
  font-weight:600;
}
.quiz-screen .question .main .title p{
  margin-top: 5px;
  color: #111;
}
.quiz-screen .question .main .options{
 margin: 40px 0px 20px;
 display: flex;
 justify-content: space-between;
 flex-wrap:wrap;
 gap:20px;
}
.quiz-screen .question .main .options .option{
  width: calc(50% -20px);
  border: 1px solid #bbb;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.quiz-screen .question .main .options .option .active{
  background: #1428cd;
  color: #fff;
}
.quiz-screen .question .control{
  padding:10px 20px;
  display:flex;
  justify-content:flex-end;
  align-items: center;
  border-top: 1px solid #eee;
}
.result-screen {
  width:100%;
  height:350px;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap:15px;
}
@keyframes progress {
  0% {
    width:0%;
    background: #555;
  }
  79% {
    width:79%;
    background: #555;
  }
  80% {
    width:80%;
    background:orangered;
  }
  100% {
    width:100%;
    background:orangered;
  }
}
